import { Injectable } from '@angular/core';
import { ServiceStatus } from '../models/service-status.enum';
import { Observable } from 'rxjs';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Kind } from '../models/kind';
import { OptionService } from './option.service';

@Injectable({
  providedIn: 'root'
})
export class KindService {

  constructor(private http: HttpClient, private options: OptionService) { }

  asyncGetSumOptions(kind: Kind): Observable<number> {
    const self = this;
    return new Observable((observer) => {
        self.options.asyncGetList(kind._id).subscribe({
          next(ris) {
            if (ris) {
              observer.next(ris.length);
            } else {
              observer.next(0);
            }
          },
          error(err) {
            observer.next(0);
          }
        });
    });
  }

  asyncPost(kind: Kind): Observable<ServiceStatus> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.post(`/api/setuppolicies/kinds`, kind, {
        headers : {
          's-t': session
        },
        observe: 'response',
        responseType: 'text'
      }).subscribe((res: HttpResponse<any>) => {
        if (res.status === 200) {
          observer.next(ServiceStatus.OK);
        } else {
          observer.next(ServiceStatus.FAILED);
        }
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

  asyncPut(kind: Kind): Observable<ServiceStatus> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.put(`/api/setuppolicies/kinds/${kind._id}`, kind, {
        headers : {
          's-t': session
        },
        observe: 'response',
        responseType: 'text'
      }).subscribe((res: HttpResponse<any>) => {
        if (res.status === 200) {
          observer.next(ServiceStatus.OK);
        } else {
          observer.next(ServiceStatus.FAILED);
        }
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

  asyncDelete(id: string): Observable<ServiceStatus> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.delete(`/api/setuppolicies/kinds/${id}`, {
        headers : {
          's-t': session
        },
        observe: 'response',
        responseType: 'text'
      }).subscribe((res: HttpResponse<any>) => {
        if (res.status === 200) {
          observer.next(ServiceStatus.OK);
        } else {
          observer.next(ServiceStatus.FAILED);
        }
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

  asyncGet(id: string): Observable<Kind> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.get(`/api/setuppolicies/kinds/${id}`, {
        headers : {
          's-t': session
        },
        observe: 'response'
      }).subscribe((data: any) => {
        console.log(data);
        const primitiveKind = data.body;
        const k = new Kind();
        k.name = primitiveKind.name;
        k.deleted = primitiveKind.deleted;
        k._id = primitiveKind._id;
        observer.next(k);
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

  asyncGetList(): Observable<Array<Kind>> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.get(`/api/setuppolicies/kinds`, {
        headers : {
          's-t': session
        },
        observe: 'response'
      }).subscribe((data: any) => {
        const primitiveKinds = data.body;
        console.log(data.body);
        const kinds: Array<Kind> = primitiveKinds.map((obj) => {
          const k = new Kind();
          k.name = obj.name;
          k.deleted = obj.deleted;
          k._id = obj._id;
          return k;
        });
        observer.next(kinds);
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }
}
