import { Injectable } from '@angular/core';
import { ServiceStatus } from '../models/service-status.enum';
import { Observable } from 'rxjs';
import { HttpClient, HttpResponse } from '@angular/common/http';

import { Option } from '../models/option';

@Injectable({
  providedIn: 'root'
})
export class OptionService {

  constructor(private http: HttpClient) { }

  asyncGetList(KindId: string): Observable<Array<Option>> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.get(`/api/setuppolicies/options/${KindId}/`, {
        headers : {
          's-t': session
        },
        observe: 'response'
      }).subscribe((data: any) => {
        const primitiveOptions = data.body;
        console.log(data.body);
        const options: Array<Option> = primitiveOptions.map((obj) => {
          const o = new Option();
          o.name = obj.name;
          o.deleted = obj.deleted;
          o._id = obj._id;
          o.kind = obj.kind;
          return o;
        });
        observer.next(options);
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

  asyncPost(kindId: string, option: Option): Observable<ServiceStatus> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.post(`/api/setuppolicies/options/${kindId}`, option, {
        headers : {
          's-t': session
        },
        observe: 'response',
        responseType: 'text'
      }).subscribe((res: HttpResponse<any>) => {
        if (res.status === 200) {
          observer.next(ServiceStatus.OK);
        } else {
          observer.next(ServiceStatus.FAILED);
        }
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

  asyncDelete(kindId: string, id: string): Observable<ServiceStatus> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.delete(`/api/setuppolicies/options/${kindId}/${id}`, {
        headers : {
          's-t': session
        },
        observe: 'response',
        responseType: 'text'
      }).subscribe((res: HttpResponse<any>) => {
        if (res.status === 200) {
          observer.next(ServiceStatus.OK);
        } else {
          observer.next(ServiceStatus.FAILED);
        }
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

  asyncGet(kindId: string, id: string): Observable<Option> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.get(`/api/setuppolicies/options/${kindId}/${id}`, {
        headers : {
          's-t': session
        },
        observe: 'response'
      }).subscribe((data: any) => {
        console.log(data);
        const primitiveOption = data.body;
        const k = new Option();
        k.name = primitiveOption.name;
        k.deleted = primitiveOption.deleted;
        k._id = primitiveOption._id;
        k.kind = primitiveOption.kind;
        observer.next(k);
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

  asyncPut(kindId: string, option: Option): Observable<ServiceStatus> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.put(`/api/setuppolicies/options/${kindId}/${option._id}`, option, {
        headers : {
          's-t': session
        },
        observe: 'response',
        responseType: 'text'
      }).subscribe((res: HttpResponse<any>) => {
        if (res.status === 200) {
          observer.next(ServiceStatus.OK);
        } else {
          observer.next(ServiceStatus.FAILED);
        }
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }
}
